<template>
<div class="page">
  <div class="weui-form">
    <div class="weui-form__text-area">
      <h2 class="weui-form__title">用户信息认证</h2>
      <div class="weui-form__desc">请填写您的身份证号，手机号和验证码。</div>
    </div>
    <div class="weui-form__control-area">
      <div class="weui-cells__group weui-cells__group_form">
        <div class="weui-cells__title">认证信息</div>
        <div class="weui-cells weui-cells_form">
          <div class="weui-cell weui-cell_active" :class="{'weui-cell_warn': !checkModel.idCode}">
            <div class="weui-cell__hd"><label class="weui-label">身份证号</label></div>
            <div class="weui-cell__bd">
              <input v-model="dataModel.idCode" class="weui-input" placeholder="填写本人身份证号"
                maxlength="18" @blur="onCheckIdCode()"/>
            </div>
          </div>
          <div class="weui-cell weui-cell_active" :class="{'weui-cell_warn': !checkModel.mobileNo}">
            <div class="weui-cell__hd"><label class="weui-label">手机号码</label></div>
            <div class="weui-cell__bd">
              <input v-model="dataModel.mobileNo" class="weui-input" placeholder="填写本人手机号码"
                maxlength="11" @blur="onCheckMobileNo()"/>
            </div>
          </div>
          <div class="weui-cell weui-cell_active weui-cell_vcode">
            <div class="weui-cell__hd"><label class="weui-label">验证码</label></div>
            <div class="weui-cell__bd">
              <input autofocus class="weui-input" type="text" @blur="onCheckVerCode()"
                pattern="[0-9]{6}" v-model="dataModel.verCode" placeholder="输入验证码" maxlength="6">
            </div>
            <div class="weui-cell__ft">
              <button class="weui-btn weui-btn_default weui-vcode-btn"
                :disabled="verCodeBtn.disabled" @click="getVerCode()">{{ verCodeBtn.text }}</button>
            </div>
          </div>
        </div>
        <div class="weui-cells__tips"><a class="weui-link" href="javascript:">收不到验证码</a></div>
      </div>
    </div>
    <div class="weui-form__opr-area">
      <a class="weui-btn weui-btn_primary" href="javascript:" :disabled="submitBtn.disabled"
        @click="doSubmit()">{{ submitBtn.text }}</a>
    </div>
    <div class="weui-form__extra-area">
      <div class="weui-footer">
        <p class="weui-footer__links">
          <a href="javascript:" class="weui-footer__link">中国工商银行上海市分行</a>
        </p>
        <p class="weui-footer__text">Copyright © 2008-2019 weui.io</p>
      </div>
    </div>
  </div>
  <div id="js_toast" style="display: none;">
      <div class="weui-mask_transparent"></div>
      <div class="weui-toast">
          <i class="weui-icon-success-no-circle weui-icon_toast"></i>
          <p class="weui-toast__content">已完成</p>
      </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'Main',
  data () {
    return {
      dataModel: {
        idCode: '',
        mobileNo: '',
        verCode: ''
      },
      checkModel: {
        idCode: true,
        mobileNo: true,
        verCode: true
      },
      verCodeBtn: {
        timer: 60,
        text: '获取验证码',
        disabled: false
      },
      submitBtn: {
        text: '确定',
        disabled: false
      }
    }
  },
  methods: {
    checkIdCode (idCode) {
      const idPattern = /^[1-9]{1}[0-9]{16}[0-9|X]{1}$/
      if (!idPattern.test(idCode)) {
        return false
      }
      const weights = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1]
      var idCodeBits = idCode.split('')
      if (idCodeBits[17] === 'X' || idCodeBits[17] === 'x') {
        idCodeBits[17] = 10
      }
      var sigma = 0
      for (var i = 0; i < 18; ++i) {
        sigma += (weights[i] * idCodeBits[i])
      }
      return sigma % 11 === 1
    },
    onCheckIdCode () {
      this.checkModel.idCode = this.checkIdCode(this.dataModel.idCode)
      if (!this.checkModel.idCode) {
        this.$root.bus.$emit('toptips', {
          errorCode: 'INVALID_ID_CODE',
          errorMsg: '无效的身份证号'
        })
      }
    },
    checkMobileNo (mobileNo) {
      const mobilePattern = /^1[0-9]{10}$/
      return mobilePattern.test(mobileNo)
    },
    onCheckMobileNo () {
      this.checkModel.mobileNo = this.checkMobileNo(this.dataModel.mobileNo)
      if (!this.checkModel.mobileNo) {
        this.$root.bus.$emit('toptips', {
          errorCode: 'INVALID_MOBILE_NO',
          errorMsg: '无效的手机号'
        })
      }
    },
    getVerCode () {
      if (this.verCodeBtn.disabled) {
        return false
      }
      this.verCodeBtn.disabled = true
      this.onCheckMobileNo()
      if (!this.checkModel.mobileNo) {
        this.verCodeBtn.disabled = false
        return
      }
      this.verCodeBtn.timer = 10
      var self = this
      this.$http.get('/ver/code/', {
        params: {
          mobileNo: self.dataModel.mobileNo
        }
      }).then(res => {
        console.log(res)
        var intervalId = setInterval(() => {
          if (self.verCodeBtn.timer > 0) {
            self.verCodeBtn.text = '剩余' + (self.verCodeBtn.timer--) + '秒'
          } else {
            self.verCodeBtn.text = '获取验证码'
            self.verCodeBtn.disabled = false
            clearInterval(intervalId)
          }
        }, 1000)
      }).catch(err => {
        self.verCodeBtn.disabled = false
        self.$errorHandler(err, self)
      })
    },
    checkVerCode (verCode) {
      return /^[0-9]{6}$/.test(verCode)
    },
    onCheckVerCode () {
      this.checkModel.verCode = this.checkVerCode(this.dataModel.verCode)
      if (!this.checkModel.verCode) {
        this.$root.bus.$emit('toptips', {
          errorCode: 'INVALID_VER_CODE',
          errorMsg: '请填写6位数字验证码'
        })
      }
    },
    isFormChecked () {
      var result = true
      for (var key in this.checkModel) {
        result = result && this.checkModel[key]
      }
      return result
    },
    doSubmit () {
      this.submitBtn.disabled = true
      this.onCheckIdCode()
      this.onCheckMobileNo()
      this.onCheckVerCode()
      if (this.isFormChecked()) {
        var self = this
        self.$http.post('/user/', this.dataModel
        ).then(res => {
          self.submitBtn.disabled = false
          self.$router.push('/result')
        }).catch(err => {
          self.submitBtn.disabled = false
          self.$errorHandler(err, self)
        })
      }
    }
  }
}
</script>
<style scoped>
.weui-cells__group_form .weui-cells__tips {
  margin-top: 8px;
  padding: 0 32px;
  color: rgba(0,0,0,0.3);
  text-align: left;
}
</style>



