<template>
  <div id="app" @click="openConsole">
    <transition :name="transitionName">
      <router-view class="child-view"></router-view>
    </transition>
    <div class="weui-toptips weui-toptips_warn"
      :style="{opacity: toptipsOpacity, display: toptipsDisplay}">{{ errorMsg }}</div>
  </div>
</template>

<script>
import VConsole from 'vconsole'

export default {
  name: 'App',
  data () {
    return {
      transitionName: 'slide-left',
      debugCounter: 1,
      debugTarget: null,
      toptipsOpacity: 0,
      toptipsDisplay: 'none',
      errorCode: '',
      errorMsg: ''
    }
  },
  watch: {
    '$route': (to, from) => {
      var toDepth = to.path.split('/').length
      var fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
    }
  },
  created () {
    var self = this
    this.$root.bus.$on('toptips', toptipsData => {
      self.errorCode = toptipsData.errorCode || ''
      self.errorMsg = toptipsData.errorMsg || ''
      self.toptipsOpacity = 1
      self.toptipsDisplay = 'block'
      setTimeout(() => {
        self.toptipsOpacity = 0
        self.toptipsDisplay = 'none'
      }, 5000)
    })
  },
  methods: {
    openConsole (event) {
      if (!this.debugTarget ||
          this.debugTarget === event.target) {
        this.debugTarget = event.target
        this.debugCounter++
      } else {
        this.debugTarget = event.target
        this.debugCounter = 1
        return
      }
      if (this.debugCounter > 10) {
        var vConsole = new VConsole()
        console.log('vConsole open:', vConsole)
      }
    }
  }
}
</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.child-view {
  position: absolute;
  transition: all .3s cubic-bezier(.55, 0, .1, 1)
}

.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}

.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}

</style>



